import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Fn } from "../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../models/baseModel";
import { TaskToDoListService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../core/TableModel";
import TableComponent from "../../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../../core/UI_FormatComponent";
import ModifyComponent from "./ModifyComponent";

export default function IndexComponent() {
    const projectKey = useParams().key;
    const sprintKey = useParams().sprintKey;

    const [projectName, setProjectName] = useState();
    const [sprintName, setSprintName] = useState();
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Nội dung", key: "content", render: (data: string, row: any) =>
                <MyButton type="link" value={data} onClick={() => {
                    handleClickModify(row.key);
                }} />
        }),
        new TableColumn({
            title: "Đã hoàn thành", key: "isCheck", render: (data: string, row: any) =>
                <MyFormItem.Switch onChange={(value: any) => {
                    handleChangeSwitch(value, row.key);
                }} value={data} />
        }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (row: string) =>
                <UIFormatDateTimeVNComponent value={row} />
        }),
        new TableColumn({
            title: "Ngày cập nhật", key: "updatedAt", render: (row: string) =>
                <UIFormatDateTimeVNComponent value={row} />
        }),
    ]

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        var paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        TaskToDoListService.GetList(projectKey, sprintKey, paginationRequest)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                    if(rs.data.pagedData.length !== 0) {
                        setProjectName(rs.data.pagedData[0].sprint.project.name);
                        setSprintName(rs.data.pagedData[0].sprint.name);
                    }
                }
            })
            .finally(() => setIsLoading(false))
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy, projectKey, sprintKey]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickModify(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function edit(key: string, body: any) {
        TaskToDoListService.GetDetail(projectKey, sprintKey, key)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    TaskToDoListService.Edit(projectKey, sprintKey, key, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            }
                            else setIsLoading(false);
                        })
                }
                else setIsLoading(false);
            })
    }


    function handleChangeSwitch(value: any, key: string) {
        setIsLoading(true);
        var body = {
            isCheck: value
        }
        edit(key, body);
    }

    return (
        <MyBox
            title="To do list"
            isLoading={isLoading}
            buttonList={() => (
                <MyButton type="primary" onClick={handleClickAdd} value="Thêm mới" />
            )} >

            <h3><strong>Tên dự án: </strong>{projectName}</h3>
            <h4><strong>Giai đoạn: </strong>{sprintName}</h4>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />

            <ModifyComponent
                projectKey={projectKey}
                sprintKey={sprintKey}
                visible={visible}
                setVisible={setVisible}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />

            <ModifyComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                projectKey={projectKey}
                sprintKey={sprintKey}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />
        </MyBox>
    );
}