import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskToDoListService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyComponent(props: any) {
    const [content, setContent] = useState();
    const [isCheck, setIsCheck] = useState(false);

    const { projectKey, sprintKey, keyData, setKeyData, isLoading, setIsLoading, visible, setVisible, loadData } = props;

    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            TaskToDoListService.GetDetail(projectKey, sprintKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setContent(rs.data.content);
                        setIsCheck(rs.data.isCheck);
                    }
                }).finally(() => setIsLoading(false));
        }
    }, [keyData, projectKey, sprintKey, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function handleOnClose() {
        setVisible(false);
        if (setKeyData !== undefined) {
            setKeyData(undefined);
        }
    }

    function handleSubmitForm() {
        let body = {
            content: content,
            isCheck: isCheck
        }

        setIsLoading(true);
        if (keyData !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        TaskToDoListService.Add(projectKey, sprintKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    loadData();
                }
            })
            .finally(() => setIsLoading(false));
    }

    function edit(body: any) {
        TaskToDoListService.GetDetail(projectKey, sprintKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    TaskToDoListService.Edit(projectKey, sprintKey, keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function handleClickDelete() {
        setIsLoading(true);
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa không?"
        }).then((check) => {
            if (check.isConfirmed) {
                doDelete();
            } else {
                setIsLoading(false);
            }
        })
    }

    function doDelete() {
        TaskToDoListService.GetDetail(projectKey, sprintKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    TaskToDoListService.Delete(projectKey, sprintKey, keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                                handleOnClose();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            title={keyData === undefined ? "Thêm mới" : "Chỉnh sửa"}
            open={visible}
            onClose={handleOnClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Nội dung"
                    value={content}
                    setValue={setContent} />

                <MyFormItem.Switch
                    label="Đã hoàn thành"
                    value={isCheck}
                    setValue={setIsCheck} />

                <MyButton
                    isLoading={isLoading}
                    htmlType="submit"
                    type="success"
                    value={keyData === undefined ? "Thêm" : "Chỉnh sửa"} />

                <MyButton
                    isLoading={isLoading}
                    value="Xóa"
                    type="danger"
                    disabled={keyData === undefined}
                    onClick={handleClickDelete} />
            </Form>
        </Drawer>
    );
}